				"html": "<p>You can use Bootstrap's <code>col-xs-*</code> classes on <code>form</code> elements. That will make radio buttons evenly spread out across the page regardless of how wide the screen might be.</p>\n<p>Nest all of your radio buttons within a <code>&#x3C;div class=\"row\"></code> element. Then nest each of them within a <code>&#x3C;div class=\"col-xs-6\"></code> element.</p>\n<pre><code>&#x3C;form action=\"/submit-cat-photo\">\n  &#x3C;div class=\"row\">\n    &#x3C;div class=\"col-xs-6\">&#x3C;label>&#x3C;input type=\"radio\" name=\"indoor-outdoor\"> Indoor&#x3C;/label>&#x3C;/div>\n    &#x3C;div class=\"col-xs-6\">&#x3C;label>&#x3C;input type=\"radio\" name=\"indoor-outdoor\"> Outdoor&#x3C;/label>&#x3C;/div>\n    &#x3C;div class=\"col-xs-6\">&#x3C;label>&#x3C;input type=\"checkbox\" name=\"personality\"> Loving&#x3C;/label>&#x3C;/div>\n    &#x3C;div class=\"col-xs-6\">&#x3C;label>&#x3C;input type=\"checkbox\" name=\"personality\"> Lazy&#x3C;/label>&#x3C;/div>\n    &#x3C;div class=\"col-xs-6\">&#x3C;label>&#x3C;input type=\"checkbox\" name=\"personality\"> Crazy&#x3C;/label>&#x3C;/div>\n  &#x3C;/div>\n  &#x3C;input type=\"text\" placeholder=\"cat photo URL\" required>\n  &#x3C;button type=\"submit\">Submit&#x3C;/button>\n&#x3C;/form>\n</code></pre>",
